.headerContainer {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
}

h1 {
  color: #fff;
  font-size: 2em;
  text-align: center;
  font-weight: 900;
  font-family: 'roboto', sans-serif;

}

.container {
  display: flex;
  grid-template-columns: auto;
  justify-content: center;
  flex-direction: row-reverse;
}

.memeProperties {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.titleText {
  margin: 10px 0 5px 0;
}

p {
  height: 2px;
}

select,
.inputText {
  height: 5vh;
  width: 30vw;
  font-size: 1.2em;
}

.btnDownload {
  position: absolute;
  top: 85vh;
  width: 20vh;
  color: #fff;
  background-color: rgb(0, 0, 0);


}

figure {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 425px;
  width: 425px;
  background-color: #000;
}

.memeImage {
  height: 400px;
  width: 400px;
}

.memeText1,
.memeText2 {
  position: absolute;
  margin: auto;
  left: 50%;
  transform: translateX(-50%);
  color: #fff;
  word-wrap: break-word;
  text-align: center;
  text-transform: uppercase;
  font-weight: bold;
  font-size: 24px;
  text-shadow: 1px 3px 3px black;
}

.memeText1 {
  top: 3vh;
}

.memeText2 {
  bottom: 3vh;
}

button {
  --glow-color: rgb(217, 176, 255);
  --glow-spread-color: rgba(191, 123, 255, 0.781);
  --enhanced-glow-color: rgb(231, 206, 255);
  --btn-color: rgb(100, 61, 136);
  border: .25em solid var(--glow-color);
  padding: 1em 3em;
  color: var(--glow-color);
  font-size: 15px;
  font-weight: bold;
  background-color: var(--btn-color);
  border-radius: 1em;
  outline: none;
  box-shadow: 0 0 1em .25em var(--glow-color),
      0 0 4em 1em var(--glow-spread-color),
      inset 0 0 .75em .25em var(--glow-color);
  text-shadow: 0 0 .5em var(--glow-color);
  position: relative;
  transition: all 0.3s;
}

button::after {
  pointer-events: none;
  content: "";
  position: absolute;
  top: 120%;
  left: 0;
  height: 100%;
  width: 100%;
  background-color: var(--glow-spread-color);
  filter: blur(2em);
  opacity: .7;
  transform: perspective(1.5em) rotateX(35deg) scale(1, .6);
}

button:hover {
  color: var(--btn-color);
  background-color: var(--glow-color);
  box-shadow: 0 0 1em .25em var(--glow-color),
      0 0 4em 2em var(--glow-spread-color),
      inset 0 0 .75em .25em var(--glow-color);
}

button:active {
  box-shadow: 0 0 0.6em .25em var(--glow-color),
      0 0 2.5em 2em var(--glow-spread-color),
      inset 0 0 .5em .25em var(--glow-color);
}

/* MEDIA QUERIES */

@media only screen and (max-width: 900px) {


  .container {
      flex-direction: column;
      height: auto;
  }

  figure {
      height: 325px;
      width: 325px;
      margin: auto;
  }

  .memeImage {
      height: 300px;
      width: 300px;
  }

  .btnDownload {
      position: relative;
      margin: 10px;
      top: 0;
      /* left: 50vh; */
      width: 220px;
      color: #fff;
      background-color: rgb(0, 0, 0);
      padding: 0;
      align-self: center;
  }
}